<template>
  <v-layout align-start>
      <v-flex>
            <v-toolbar flat color="#b2dfdb">
              <v-toolbar-title>Ventas</v-toolbar-title>
              <v-divider
                  class="mx-2"
                  inset
                  vertical
              ></v-divider>
              <v-spacer></v-spacer>
              <v-text-field v-if="verNuevo==0" class="text-xs-center" v-model="search" append-icon="search" label="Búsqueda" single-line hide-details></v-text-field>
              <v-btn v-if="verNuevo==0" color="primary" dark class="mb-2" @click="listar()">Buscar</v-btn>
              <v-spacer></v-spacer>
              <v-btn v-if="verNuevo==0" color="primary" dark class="mb-2" @click="mostrarNuevo()">Nuevo</v-btn>
              <!-- AD MODAL STARTS HERE ---->
              <v-dialog v-model="verArticulos" max-width="1000px">
                  <v-card>
                      <v-card-title>
                        <span class="headline">Seleccione un artículo</span>
                      </v-card-title>
                      <v-card-text>
                        <v-container grid-list-md>
                            <v-layout wrap>
                              <v-flex xs12 sm12 lg12 xl12>
                                  <v-text-field append-icon="search" 
                                                class="text-xs-center" 
                                                v-model="texto"
                                                label="Ingrese artículo a buscar"
                                                @keyup.enter="listarArticulo()"
                                                >
                                  </v-text-field>
                                  <template>
                                      <v-data-table
                                            :headers="cabeceraArticulos"
                                            :items="articulos"
                                            class="elevation-3"
                                            >
                                            <template slot="items" slot-scope="props">
                                                  <td class="text-xs-center">
                                                    <v-icon small 
                                                            class="mr-2"
                                                            @click="agregarDetalle(props.item)" 
                                                            >
                                                            add
                                                    </v-icon>
                                                  </td>
                                                  <td>{{ props.item.nombre }}</td>
                                                  <td>{{ props.item.categoria}}</td>
                                                  <td>{{ props.item.descripcion}}</td>
                                                  <td>{{ props.item.stock}}</td>
                                                  <td>{{ props.item.precio_venta}}</td>
                                            </template>
                                            <template slot="no-data">
                                                <h3>No hay artículos para mostrar</h3>
                                            </template>
                                      </v-data-table>
                                  </template>
                              </v-flex>
                            </v-layout>
                        </v-container>
                      </v-card-text>
                      <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-flex v-if="errorArticulo" xs12 sm2 lg2 xl2>
                            <div v-text="errorArticulo" class="red--text">
                            </div>
                        </v-flex>
                        <v-spacer></v-spacer>
                        <v-btn @click="ocultarArticulos()" color="blue darken" flat>
                            Cancelar
                        </v-btn>
                      </v-card-actions>
                  </v-card>
              </v-dialog>
              <v-dialog v-model="adModal" max-width="290">
                  <v-card>
                      <v-card-title class="justify-center" v-if="adAccion==1">
                        <span class="headline">¿Activar Usuario?</span>
                      </v-card-title>
                      <v-card-title class="justify-center" v-if="adAccion==2">
                        <span class="headline">¿Anular Venta?</span>
                      </v-card-title>
                      <v-card-text>
                           Estas a punto de 
                           <span v-if="adAccion==1">Activar</span>
                           <span v-if="adAccion==2">Anular</span>
                           el ingreso con num. comprobante: {{adNombre}}                           
                      </v-card-text>
                      <v-card-actions>
                        <v-spacer></v-spacer>
                          <v-btn v-if="adAccion" color="green darken-1" flat="flat" @click="activarDesactivarCerrar">Cancelar</v-btn>
                          <v-btn v-if="adAccion==1" color="orange darken-1" flat="flat" @click="activar">Activar</v-btn>
                          <v-btn v-if="adAccion==2" color="orange darken-1" flat="flat" @click="desactivar">Anular</v-btn>
                        <v-spacer></v-spacer>
                    </v-card-actions>
                  </v-card>
              </v-dialog>
              <v-dialog v-model="comprobanteModal" max-width="1000px">
                <v-card>
                  <v-card-text>
                    <v-btn @click="crearPDF()" color="success">
                      <v-icon>print</v-icon>
                    </v-btn>
                    <div id="factura">
                        <header>
                            <div id="logo">
                                <img src="data:image/png;base64," id="imagen">
                            </div>
                            <div id="datos">
                                <p id="encabezado">
                                    <b>ZNINT</b><br>Calle Berlín 669, San Isidro - Lima, Perú<br>Telefono:(+51)123456789<br>Email:mortydrc@gmail.com
                                </p>
                            </div>
                            <div id="fact">
                                <p>{{tipo_comprobante}}<br>
                                {{serie_comprobante}}-{{num_comprobante}}<br>
                                {{fecha_hora}}</p>
                            </div>
                        </header>
                        <br>
                        <section>
                            <div>
                              <table id="facliente">
                                                <tbody>
                                                    <tr>
                                                        <td id="cliente">
                                                            <strong>Sr(a). {{cliente}}</strong><br>
                                                            <strong>Documento:</strong> {{num_documento}}<br>
                                                            <strong>Dirección:</strong> {{direccion}}<br>
                                                            <strong>Teléfono:</strong> {{telefono}}<br>
                                                            <strong>Email:</strong> {{email}}
                                                        </td>
                                                    </tr>
                                                </tbody>
                                            </table>
                                        </div>
                                    </section>
                                    <br>
                                    <section>
                                        <div>
                                            <table id="facarticulo">
                                                <thead>
                                                    <tr id="fa">
                                                        <th>CANTIDAD</th>
                                                        <th>DESCRIPCION</th>
                                                        <th>PRECIO UNIT</th>
                                                        <th>DESC.</th>
                                                        <th>PRECIO TOTAL</th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    
                                                    <tr v-for="det in detalles" :key="det.iddetalle_venta">
                                                        <td style="text-align:center;">{{det.cantidad}}</td>
                                                        <td>{{det.articulo}}</td>
                                                        <td style="text-align:right;">{{det.precio.toFixed(2)}}</td>
                                                        <td style="text-align:right;">{{det.descuento.toFixed(2)}}</td>
                                                        <td style="text-align:right;">{{(det.cantidad*det.precio-det.descuento).toFixed(2)}}</td>
                                                    </tr>
                                                </tbody>
                                                <tfoot>
                                                    <tr>
                                                        <th></th>
                                                        <th></th>
                                                        <th></th>
                                                        <th style="text-align:right;">SUBTOTAL</th>
                                                        <th style="text-align:right;"> $ {{totalParcial=(total-totalImpuesto).toFixed(2)}} </th>
                                                    </tr>
                                                    <tr>
                                                        <th></th>
                                                        <th></th>
                                                        <th></th>
                                                        <th style="text-align:right;">IGV({{impuesto}}%)</th>
                                                        <th style="text-align:right;">$ {{totalImpuesto=((total*impuesto)/(100+impuesto)).toFixed(2)}}</th>
                                                    </tr>
                                                    <tr>
                                                        <th></th>
                                                        <th></th>
                                                        <th></th>
                                                        <th style="text-align:right;">TOTAL</th>
                                                        <th style="text-align:right;">$ {{total=(calcularTotal).toFixed(2)}}</th>
                                                    </tr>
                                                </tfoot>
                                            </table>
                                        </div>
                                    </section>
                                    <br>
                                    <br>
                                    <footer>
                                        <div id="gracias">
                                            <p><b>Gracias por su compra!</b></p>
                                        </div>
                                    </footer>
                    </div>
                    <v-btn @click="ocultarComprobante()" color="blue-darken-1" flat>Cancelar</v-btn>
                    
                  </v-card-text>
                </v-card>
              </v-dialog>
            </v-toolbar>
            <!-- DATA TABLE STARTS HERE -->
            <v-data-table
            :headers="headers"
            :items="ventas"
            :search="search"
            class="elevation-3"
            v-if="verNuevo==0"
            >
            <template slot="items" slot-scope="props">
                  <td class="text-xs-center">
                    <v-icon small 
                            class="mr-2" 
                            @click="verDetalles(props.item)"
                            >
                            tab
                    </v-icon>
                    <v-icon small 
                            class="mr-2" 
                            @click="mostrarComprobante(props.item)"
                            >
                            print
                    </v-icon>
                    <template v-if="props.item.estado=='Aceptado'">
                      <v-icon small @click="activarDesactivarMostrar(2,props.item)">block</v-icon>
                    </template>
                  </td>
                  <td class="text-xs-left">{{ props.item.usuario }}</td>
                  <td class="text-xs-left">{{ props.item.cliente }}</td>
                  <td class="text-xs-left">{{ props.item.tipo_comprobante }}</td>
                  <td class="text-xs-left">{{ props.item.serie_comprobante }}</td>
                  <td class="text-xs-left">{{ props.item.num_comprobante }}</td>
                  <td class="text-xs-left">{{ props.item.fecha_hora }}</td>
                  <td class="text-xs-left">{{ props.item.impuesto }}</td>
                  <td class="text-xs-left">{{ props.item.total }}</td>
                  <td> 
                    <div v-if="props.item.estado=='Aceptado'">
                        <span class="blue--text">Aceptado</span>
                    </div>
                    <div v-else>
                        <span class="red--text">{{props.item.estado}}</span>
                    </div>
                  </td>
            </template>
            <template slot="no-data">
                <v-btn color="primary" @click="listar">Resetear</v-btn>
            </template>
            </v-data-table>
            <v-container grid-list-xs class="pa-4 white" v-if="verNuevo">
               <v-layout row wrap>
                 <v-flex xs12 sm4 lg4 xl4>
                    <v-select
                      :items="comprobantes"
                      v-model="tipo_comprobante"
                      label="Tipo Comprobante"
                    ></v-select>
                 </v-flex>
                 <v-flex xs12 sm4 lg4 xl4>
                    <v-text-field
                      v-model="serie_comprobante"
                      label="Serie Comprobante"
                    ></v-text-field>
                 </v-flex>
                 <v-flex xs12 sm4 lg4 xl4>
                    <v-text-field
                      v-model="num_comprobante"
                      label="Número Comprobante"
                    ></v-text-field>
                 </v-flex>
                 <v-flex xs12 sm8 lg8 xl8>
                    <v-select
                      :items="clientes"
                      v-model="idcliente"
                      label="Cliente"
                    ></v-select>
                 </v-flex>
                 <v-flex xs12 sm4 lg4 xl4>
                    <v-text-field
                      type="number"
                      v-model="impuesto"
                      label="Impuesto"
                    ></v-text-field>      
                 </v-flex>
                 <v-flex xs12 sm8 lg8 xl8>
                    <v-text-field
                      @keyup.enter="buscarCodigo()"
                      v-model="codigo"
                      label="Código"
                    ></v-text-field>      
                 </v-flex>
                 <v-flex xs12 sm2 lg2 xl2>
                    <v-btn @click="mostrarArticulos()" small fab dark color="teal">
                      <v-icon dark>list</v-icon>
                    </v-btn>
                 </v-flex>
                 <v-flex v-if="errorArticulo" xs12 sm2 lg2 xl2>
                    <div v-text="errorArticulo" class="red--text">
                    </div>
                 </v-flex>
                 <v-flex xs12 sm12 lg12 xl12>
                        <v-data-table
                              :headers="cabeceraDetalles"
                              :items="detalles"
                              hide-actions
                              class="elevation-3"
                              >
                              <template slot="items" slot-scope="props">
                                    <td class="text-xs-center">
                                      <v-icon small 
                                              class="mr-2"
                                              @click="eliminarDetalle(detalles,props.item)" 
                                              >
                                              delete
                                      </v-icon>
                                    </td>
                                    <td class="text-xs-center">{{ props.item.articulo }}</td>
                                    <td class="text-xs-center"><v-text-field type="number" v-model="props.item.cantidad"></v-text-field></td>
                                    <td class="text-xs-center"><v-text-field type="number" v-model="props.item.precio"></v-text-field></td>
                                    <td class="text-xs-center"><v-text-field type="number" v-model="props.item.descuento"></v-text-field></td>
                                    <td class="text-xs-center">$ {{ props.item.cantidad * props.item.precio - props.item.descuento}}</td>
                              </template>
                              <template slot="no-data">
                                  <h3>No hay artículos agregados al detalle</h3>
                              </template>
                        </v-data-table>
                        <v-flex class="text-xs-right">
                          <strong>Total Parcial: </strong>$ {{totalParcial=(total-totalImpuesto).toFixed(2)}}
                        </v-flex>
                        <v-flex class="text-xs-right">
                          <strong>Total Impuesto: </strong>$ {{totalImpuesto=((total*impuesto)/(100+impuesto)).toFixed(2)}}
                        </v-flex>
                        <v-flex class="text-xs-right">
                          <strong>Total Neto: </strong>$ {{total=(calcularTotal).toFixed(2)}}
                        </v-flex>
                 </v-flex>
                 <v-flex xs12 sm12 lg12 xl12 >
                    <div class="red--text" v-for="v in validamensaje" :key="v" v-text="v">
                    </div>  
                 </v-flex>
                 <v-flex xs12 sm12 lg12 xl12 >
                      <v-btn @click="ocultarNuevo()" color="blue darken-1" flat>Cancelar</v-btn>
                      <v-btn v-if="verDet" @click="guardar()" color="success">Guardar</v-btn>
                 </v-flex>
               </v-layout>
            </v-container>
      </v-flex>
  </v-layout>
</template>

<script>
  import axios from 'axios'
  import jsPDF from 'jspdf'
  import html2canvas from 'html2canvas'

  export default {
    data(){
      return {
          ventas:[],
          dialog: false,
          headers: [
            { text: 'Opciones', value: 'opciones',sortable: false , align : 'center'},
            { text: 'Usuario', value: 'usuario', align : 'center'},
            { text: 'Cliente', value: 'cliente', align : 'center'},
            { text: 'Tipo Comprobante', value: 'tipo_comprobante', align : 'center'},
            { text: 'Serie Comprobante', value: 'serie_comprobante', align : 'center',sortable: false},
            { text: 'Número Comprobante', value: 'num_comprobante', align : 'center',sortable: false},   
            { text: 'Fecha', value: 'fecha_hora', align : 'center', sortable : false},                       
            { text: 'Impuesto', value: 'impuesto',sortable: false },
            { text: 'Total', value: 'total',sortable: false },
            { text: 'Estado', value: 'estado'},
          ],
          cabeceraDetalles: [
            { text: 'Borrar', value: 'borrar',sortable: false , align : 'center'},
            { text: 'Artículo', value: 'articulo',sortable: false, align : 'center'},
            { text: 'Cantidad', value: 'cantidad',sortable: false, align : 'center'},
            { text: 'Precio', value: 'precio',sortable: false, align : 'center'},
            { text: 'Descuento', value: 'descuento',sortable: false, align : 'center'},
            { text: 'Subtotal', value: 'subtotal',sortable: false, align : 'center'},
          ],
          detalles:[],
          search: '',
          id: '',
          idcliente : '', 
          clientes : [],
          tipo_comprobante: '',
          comprobantes: ['FACTURA','BOLETA','TICKET','GUIA'],
          serie_comprobante : '',
          num_comprobante : '',
          impuesto: 18,
          codigo: '',
          verNuevo : 0,
          errorArticulo:null,
          totalParcial:0,
          totalImpuesto:0,
          total:0,
          cabeceraArticulos: [
            { text: 'Seleccionar', value: 'seleccionar',sortable: false , align : 'center'},
            { text: 'Artículo', value: 'articulo',sortable: false, align : 'center'},
            { text: 'Categoria', value: 'categoria',sortable: false, align : 'center'},
            { text: 'Descripcion', value: 'descripcion',sortable: false, align : 'center'},
            { text: 'Stock', value: 'stock',sortable: false, align : 'center'},
            { text: 'Precio Venta', value: 'precio_venta',sortable: false, align : 'center'},
          ],
          articulos:[],
          verArticulos: 0,
          verDet: 0,
          texto:'',
          valida: 0,
          validamensaje: [],
          adModal: false,
          adAccion: 0,
          adNombre : '',
          adId : '',
          comprobanteModal: 0,
          cliente: '',
          fecha_hora: '',
          num_documento: '',
          direccion: '',
          telefono: '',
          email: '',
      }
    },
    computed: {
        calcularTotal:function(){
          var resultado = 0.0;
          for(var i=0;i<this.detalles.length;i++){
            resultado = resultado+(this.detalles[i].precio*this.detalles[i].cantidad-this.detalles[i].descuento)
          }
          return resultado;
        }
    },

    watch: {
      dialog (val) {
        val || this.close()
      }
    },

    created () {
      this.listar();
      this.select();
    },

    methods: {

      crearPDF(){
        var quotes = document.getElementById('factura');
        html2canvas(quotes).then(function(canvas){
          var imgData = canvas.toDataURL('image/png');
          var imgWidth = 210;
          var papeHeight = 295;
          var imgHeight = canvas.height * imgWidth / canvas.width;
          var heightLeft = imgHeight;
          var doc = new jsPDF('p','mm','a4');
          var posicion = 0;

          doc.addImage(imgData, 'PNG', 0, posicion, imgWidth, imgHeight);
          doc.save('ComprobanteVenta.pdf')
        });
      },

      mostrarComprobante(item){
        this.limpiar();
        this.tipo_comprobante= item.tipo_comprobante;
        this.serie_comprobante= item.serie_comprobante;
        this.num_comprobante= item.num_comprobante;
        this.cliente= item.cliente;
        this.num_documento= item.num_documento;
        this.direccion= item.direccion;
        this.telefono= item.telefono;
        this.email= item.email;
        this.fecha_hora= item.fecha_hora;
        this.impuesto= item.impuesto;
        this.listarDetalles(item.idventa);
        this.comprobanteModal = 1;
      },
      
      ocultarComprobante(){
        this.comprobanteModal = 0;
        this.limpiar();
      },

      mostrarNuevo(){
        this.verNuevo = 1;
        this.verDet = 1;
      },

      ocultarNuevo(){
        this.verNuevo = 0;
        this.limpiar();
      },

      buscarCodigo(){
        let me = this;
        me.errorArticulo = null;
        let header = {"Authorization": "Bearer " + this.$store.state.token}; 
        let configuracion = {headers : header};
        axios.get('api/Articulos/BuscarCodigoVenta/'+this.codigo,configuracion)
        .then(function(response){
          // console.log(response)
          me.agregarDetalle(response.data);
          me.codigo='';
        }).catch(function(error){
          console.log(error);
          me.errorArticulo = 'No existe el artículo';
        })
      },

      listarArticulo(){
        let me = this;
        let header = {"Authorization": "Bearer " + this.$store.state.token}; 
        let configuracion = {headers : header};
        axios.get('api/Articulos/ListarVenta/'+ me.texto, configuracion).then(function(response){
          //console.log(response)
          me.articulos=response.data;
        }).catch(function(error){
          //console.log(error)
        })
      },

      mostrarArticulos(){
        this.verArticulos = 1;
      },

      ocultarArticulos(){
        this.verArticulos = 0; 
      },

      agregarDetalle(data = []){
        this.errorArticulo=null;
        if (this.encuentra(data['idarticulo'])){
            this.errorArticulo = "El artículo ya ha sido agregado";
        }else{
            this.detalles.push(
              {idarticulo: data['idarticulo'],
              articulo: data['nombre'],
              cantidad:1,
              precio:data['precio_venta'],
              descuento:0}
            );
        }
          
      },

      encuentra(id){
        // console.log("Entre al método encuentra!")
        var sw=0;
        for (var i=0;i<this.detalles.length;i++){
            // console.log("Estoy buscando al un id idéntico al mio para que no vuelva a ser ingresado en la tabla")
          if(this.detalles[i].idarticulo==id){
            // console.log("Entre al if!")
            sw=1;
          }
        }
        return sw;
      },

      eliminarDetalle(arr,item){
        var i = arr.indexOf(item);
        if(i!==-1){ //Es diferente de -1 (no esta vacio)
          arr.splice(i,1);
          console.log("Eliminaste un elemento del array de detalle de ingreso");
        }
        if(this.detalles.length==0){
          this.errorArticulo = '';
          console.log("El array de detalles esta vacio y el mensaje de error es: "+ this.errorArticulo)      
        }
      },

      listar(){
        let me = this;
        let header = {"Authorization": "Bearer " + this.$store.state.token}; 
        let configuracion = {headers : header};
        let url = '';
        if(!me.search){
            url='api/Ventas/Listar';
        } else{
            url='api/Ventas/ListarFiltro/'+ me.search;
        }
        axios.get(url,configuracion).then(function(response){
          //console.log(response)
          me.ventas=response.data;
        }).catch(function(error){
          //console.log(error)
        })
      },

      listarDetalles(id){
        let me = this;
        let header = {"Authorization": "Bearer " + this.$store.state.token}; 
        let configuracion = {headers : header};
        axios.get('api/Ventas/ListarDetalles/'+id,configuracion).then(function(response){
          //console.log(response)
          me.detalles=response.data;
        }).catch(function(error){
          //console.log(error)
        })
      },

      verDetalles(item){
          this.limpiar();
          this.tipo_comprobante = item.tipo_comprobante;
          this.serie_comprobante = item.serie_comprobante;
          this.num_comprobante = item.num_comprobante;
          this.idcliente = item.idcliente;
          this.impuesto = item.impuesto;
          this.listarDetalles(item.idventa);
          this.verNuevo = 1; /////Abre el dialog para ver los detalles del ingreso
          this.verDet = 0;   ////Le digo que cuando quiero ver los detalles me esconda el boton guardar
      },

      select(){   //Seleccionar Clientes
        let me = this;
        var clientesArray = [] ;
        let header = {"Authorization": "Bearer " + this.$store.state.token}; 
        let configuracion = {headers : header};
        axios.get('api/Personas/SelectClientes',configuracion).then(function(response){
          //console.log(response)
          clientesArray=response.data;
          clientesArray.map(function(x){
              me.clientes.push({text: x.nombre,value: x.idpersona});
          });
        }).catch(function(error){
          //console.log(error)
        })
      },

      close () {
        this.dialog = false
        this.limpiar();
      },

      limpiar(){
          this.id="";
          this.idcliente="";
          this.tipo_comprobante="";
          this.serie_comprobante="";
          this.num_comprobante="";
          this.impuesto="18";
          this.codigo="";
          this.detalles=[];
          this.total= 0;
          this.totalImpuesto = 0;
          this.totalParcial = 0;
      },

      guardar () {
        if(this.validar()){   // validar() = 1
          return;
        }
        let header = {"Authorization": "Bearer " + this.$store.state.token}; 
        let configuracion = {headers : header};
          //Code to save
          let me = this;                 
          axios.post('api/Ventas/Crear',{
            'idcliente': me.idcliente,
            'idusuario': me.$store.state.usuario.idusuario,
            'tipo_comprobante': me.tipo_comprobante,
            'serie_comprobante': me.serie_comprobante,
            'num_comprobante': me.num_comprobante,
            'impuesto': me.impuesto,
            'total': me.total,
            'detalles': me.detalles,
            },configuracion).then(function(response){
              me.ocultarNuevo();
              me.listar();
              me.limpiar();
            }).catch(function(error){
              console.log(error);
            })
      },

      validar() {
          this.valida = 0;
          this.validamensaje = [];

          if(!this.idcliente){
              this.validamensaje.push("Seleccione un cliente.")
          }
          if(!this.tipo_comprobante){
              this.validamensaje.push("Seleccione un tipo de comprobante.")
          }
          if(!this.num_comprobante){
              this.validamensaje.push("Ingrese un número del comprobante.")
          }
          if(!this.impuesto || this.impuesto < 0){
              this.validamensaje.push("Ingrese un impuesto válido.")
          }
          if(this.detalles.length<=0){
              this.validamensaje.push("Ingrese al menos un artículo al detalle.")
          }
          if(this.validamensaje.length){
              this.valida=1;
          }
          return this.valida;
      },

      activarDesactivarMostrar(accion,item){
          this.adModal = true;
          this.adNombre = item.num_comprobante;
          this.adId = item.idventa;

          if(accion==1){
            this.adAccion = 1;
          }
          else if(accion==2){
            this.adAccion = 2;           
          }
          else{
              this.adModal = false;
          }
      },
      
      activarDesactivarCerrar(){
        this.adModal=false;
      },

      // activar(){
      //     let me = this;
      //     let header = {"Authorization": "Bearer " + this.$store.state.token}; 
      //     let configuracion = {headers : header};
      //       axios.put('api/Usuarios/Activar/'+this.adId,{},configuracion).then(function(response){
      //           me.adModal=false,
      //           me.adAccion = 0,
      //           me.adId = "",
      //           me.adNombre = "";
      //           me.listar();
      //         }).catch(function(error){
      //           console.log(error);
      //         })
      // },

      desactivar(){
          let me = this;
          let header = {"Authorization": "Bearer " + this.$store.state.token}; 
          let configuracion = {headers : header};
            axios.put('api/Ventas/Anular/'+this.adId,{},configuracion).then(function(response){
                me.adModal=false,
                me.adAccion = 0,
                me.adId='',
                me.adNombre = '';
                me.listar();
              }).catch(function(error){
                console.log(error);
              })
      }     
    }
  }
</script>
