import Vue from 'vue'
import Vuex from 'vuex'
import decode from 'jwt-decode'
import router from './router'

/////////////LUGAR DONDE SE GUARDA EL ESTADO ///////////////////////////////////////////////////////////

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    token: null,
    usuario: null,
    // nombre_usuario : null, 
  },
  mutations: {    ////CAMBIAR  VALORES PARA EL STATE
    setToken(state,token){
      state.token = token;
    },
    setUsuario(state,usuario){
      state.usuario = usuario;
    },
    // setNombreUsuario(state,nombre_usuario){
    //   state.nombre_usuario = nombre_usuario;
    // }
  },
  actions: {    //////REALIZAN MUTACIONES
    guardarToken({commit},token){     //////commit permite  realizar una mutacion o acceder al state
      commit("setToken",token)
      commit("setUsuario",decode(token))
      localStorage.setItem("token",token)
    },
    autoLogin({commit}){    /////VERIFICAR QUE EL USUARIO YA TIENE GENERADO UN TOKEN  ,QUE EXISTA
      let token = localStorage.getItem("token")
      if (token) {
        commit("setToken", token)
        commit("setUsuario", decode(token))
      }
      router.push({name: 'home'}) ////////////REDIRECCIONA AL USUARIO A HOME(COMPONENTE DE INICIO)
    },
    salir({commit}){        //////ELIMIAR EL TOKEN REGISTRADO
      commit("setToken", null)
      commit("setUsuario", null)
      localStorage.removeItem("token")
      router.push({name : 'login'})
    }
  }
})
